body{
  font-family: 'Roboto Mono', monospace;
  overflow:auto;
  width: 100%;
  height: 1000px;
  text-align: center;
  margin: 0;
  background-color: rgb(46, 35, 35) ;
  color: #a6a6a6;
  background-image: url('https://imgur.com/pZEsll0.gif');
  background-repeat: no-repeat;
  background-size: cover; 

}


.containerintro {
  padding-top: 15rem;
  top: 7rem;
  background-color: rgba(0, 18, 106, 0.687);
  height: 100%;
  
}
a{
  text-decoration: none;
}
.containerintro h1{
  color: white;
  font-size: 190%;
   overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: .1em solid #a6a6a6; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: .15em;
  -webkit-animation: 
    typing 3.5s steps(10, end) forwards ,
    blink-caret .75s step-end infinite ;
          animation: 
    typing 3.5s steps(10, end) forwards ,
    blink-caret .75s step-end infinite ;
    
}
@media (min-width: 1170px){
/* The typing effect */
@-webkit-keyframes typing {
  from { width: 0 }
  to { width: 28% }
}
@keyframes typing {
  from { width: 0 }
  to { width: 28% }
}
}
@media (max-width: 1169.99px){
/* The typing effect */
@-webkit-keyframes typing {
  from { width: 0 }
  to { width: 37% }
}
@keyframes typing {
  from { width: 0 }
  to { width: 37% }
}
}
@media (max-width: 969.99px){
/* The typing effect */
@-webkit-keyframes typing {
  from { width: 0 }
  to { width: 45% }
}
@keyframes typing {
  from { width: 0 }
  to { width: 45% }
}
}
@media (max-width: 769.99px){
/* The typing effect */
@-webkit-keyframes typing {
  from { width: 0 }
  to { width: 70% }
}
@keyframes typing {
  from { width: 0 }
  to { width: 70% }
}
}
@media (max-width: 469.99px){
  .container h1{
    letter-spacing: 80%; 
    font-size: 150%;
  }
/* The typing effect */
@-webkit-keyframes typing {
  from { width: 0 }
  to { width: 85% }
}
@keyframes typing {
  from { width: 0 }
  to { width: 85% }
}
}
/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
  from { border-color: transparent }
  to { border-color: transparent ;
        opacity: 0; }
  50% { border-color: rgb(0, 56, 211); }
}
@keyframes blink-caret {
  from { border-color: transparent }
  to { border-color: transparent ;
        opacity: 0; }
  50% { border-color: rgb(0, 56, 211); }
}












